<!-- PreviewSection.vue -->
<template>
  <section class="preview-section">
    <div class="preview-header">
       
      <button class="fullscreen-btn" @click="generatePosterPreview">
        
      </button>
    </div>
    <!--    -->

    <div class="preview-content">
      <img
        v-if="posterImage"
        :src="posterImage"
        alt="Preview"
        class="preview-image"
      />
    </div>
  </section>
</template>

<script setup>
import { ref, watch, onMounted } from "vue";
import { store } from "./store";
import html2canvas from "html2canvas";

watch(
  () => store.posterData,
  () => {
    generatePosterPreview();
  },
  { deep: true }
);

//     
onMounted(() => {
  generatePosterPreview();
});

const posterImage = ref(null); //      

const generatePosterPreview = async () => {
  //    PosterSection  
  const posterElement = document.querySelector(".poster");
  if (!posterElement) return;

  try {
    //   PosterSection  
    const canvas = await html2canvas(posterElement, {
      scale: 0.2, //      ()
      useCORS: true, //    
    });
    posterImage.value = canvas.toDataURL(); //  canvas  URI 
  } catch (error) {
    console.error("  :", error);
  }
};
</script>

<style>
* {
  margin: 0;

  padding: 0;

  box-sizing: border-box;

  font-family: Arial, sans-serif;
}

.preview-section {
  flex: 1;
  background-color: #333;
  color: white;
  padding: 10px 20px;
  position: relative;
  max-height: 92vh;
}

.preview-header {
  font-size: 16px;
  color: #979797;
  margin: 5px;
  display: flex;
  position: absolute;
  flex-direction: column;
  align-items: flex-end;
  gap: 10px;
  right: 10px;
}

.preview-content {
  height: calc(100% - 30px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.preview-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain; /*        */
}

.fullscreen-btn {
  padding: 10px;
  right: 10px;
  color: white;
  background-color: transparent;
  border: 1px solid white;
  border-radius: 3px;
  cursor: pointer;
}

